<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto elevation-2">
        <v-card-text>
          <v-btn fab dark small color="primary" @click="abrirDialogoNuevoMsg(1)" >
            <v-icon dark>mail</v-icon>
          </v-btn>
          <v-row>
            <v-col md="6" lg="6" sm="12">
              <v-treeview
                v-model="mensajes"
                :items="arrTiposMsg"
                rounded
                hoverable
                activatable
                item-key="tipo"
                open-on-click
              >
                <template v-slot:prepend="{ item, open }">
                  <v-icon color="primary" v-if="!item.child">
                    {{ open ? 'mdi-folder-open' : 'mdi-folder' }}
                  </v-icon>
                  <v-icon color="primary" v-else>
                    mail
                  </v-icon>
                </template>
                <template v-slot:label="{ item }">
                  <a @click="mostrarmensaje(item)">{{ item.name }}</a>
                </template>
              </v-treeview>
            </v-col>
            <v-col md="6" lg="6" sm="12">
              <div id="txtmsg" style="color:black;border: 2px solid #3F51B5;overflow-y: scroll;height:300px;border-radius: 5px;padding: 5px 5px 5px 5px">
                <span v-html="txtmensaje"></span>
              </div>
              <br>
              <v-btn
                small
                color="primary"
                @click="abrirDialogoNuevoMsg(0)"
                :disabled="this.txtmensaje == ''"
              >
                Anexar Respuesta
              </v-btn>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
     </v-flex>
     <v-dialog
      v-model="dialog_nuevomsg"
      persistent
      max-width="800"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center">
          {{ tituloanexarmsg}}
        </v-card-title>
        <v-card-text>
          <v-textarea
            label="Mensaje"
            outlined
            background-color="white"
            v-model="txtnuevomsg"
            rows="8"
          >
          </v-textarea>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="guardarMensaje" :disabled="txtnuevomsg==''">Agregar Respuesta</v-btn>
          <v-btn color="primary" dark text @click="dialog_nuevomsg=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
export default {
  name: 'anunciosTabMsg',
  props: ['itemactual'],
  data () {
    return {
      txtmensaje: '',
      mensajes: [],
      arrTiposMsg: [
        { id: 'E', name: 'Enviados' },
        { id: 'R', name: 'Recibidos' },
        { id: 'S', name: 'Sin Leer' },
        { id: 'L', name: 'Leídos' }
      ],
      dataMsg: [],
      dataMsgHijos: [],
      dataMsgEnviados: [],
      dataMsgRecibidos: [],
      dataMsgSinLeer: [],
      dataMsgLeidos: [],
      idpadremsg: '',
      dialog_nuevomsg: false,
      tituloanexarmsg: '',
      nuevomsg: 0,
      txtnuevomsg: ''
    }
  },
  methods: {
    async getMensajes () {
      try {
        const { data } = await anunciosProcAdquisicion.getMsgConvocatoriaRegistro({
          propconvid: this.itemactual.id
        })
        this.dataMsg = data.data
        this.getEnviados()
        this.getRecibidos()
        this.getSinLeer()
        this.getLeidos()
        this.getArmarEstructuraMsgt()
      } catch (error) {
      } finally {
      }
    },
    async getMensajesHijos () {
      try {
        const { data } = await anunciosProcAdquisicion.getMsgHijos({
          propconvid: this.itemactual.id
        })
        this.dataMsgHijos = data.data
      } catch (error) {
      } finally {
      }
    },
    getArmarEstructuraMsgt () {
      this.arrTiposMsg = [
        {
          id: 'E',
          name: 'Enviados (' + this.dataMsgEnviados.length + ')',
          children: this.dataMsgEnviados
        },
        {
          id: 'R',
          name: 'Recibidos (' + this.dataMsgRecibidos.length + ')',
          children: this.dataMsgRecibidos
        },
        {
          id: 'S',
          name: 'Sin Leer (' + this.dataMsgSinLeer.length + ')',
          children: this.dataMsgSinLeer
        },
        {
          id: 'L',
          name: 'Leídos (' + this.dataMsgLeidos.length + ')',
          children: this.dataMsgLeidos
        }
      ]
    },
    getEnviados () {
      this.dataMsgEnviados = []
      const enviados = this.dataMsg.filter(item => item.userid === this.$session.get('usuarioid'))
      for (let index = 0; index < enviados.length; index++) {
        const element = enviados[index]

        this.dataMsgEnviados.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
    },
    getRecibidos () {
      this.dataMsgRecibidos = []
      const arrMsg = this.dataMsg.filter(item => item.userid === 0)
      for (let index = 0; index < arrMsg.length; index++) {
        const element = arrMsg[index]

        this.dataMsgRecibidos.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
    },
    getLeidos () {
      this.dataMsgLeidos = []
      const arrMsg = this.dataMsg.filter(item => item.userid === 0 && item.leido === 1)
      for (let index = 0; index < arrMsg.length; index++) {
        const element = arrMsg[index]

        this.dataMsgLeidos.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
      const arrMsgHij = this.dataMsgHijos.filter(item => item.userid === 0 && item.leido === 1)
      for (let index = 0; index < arrMsgHij.length; index++) {
        const element = arrMsgHij[index]

        this.dataMsgLeidos.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
    },
    getSinLeer () {
      this.dataMsgSinLeer = []
      const arrMsg = this.dataMsg.filter(item => item.userid === 0 && item.leido === 0)
      for (let index = 0; index < arrMsg.length; index++) {
        const element = arrMsg[index]

        this.dataMsgSinLeer.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
      const arrMsgHij = this.dataMsgHijos.filter(item => item.userid === 0 && item.leido === 0)
      for (let index = 0; index < arrMsgHij.length; index++) {
        const element = arrMsgHij[index]

        this.dataMsgSinLeer.push({ id: element.codigo, name: element.fecha, child: 1, padre: element.padre })
      }
    },
    async buscarMensaje (id) {
      let mensaje = this.dataMsg.filter(item => item.codigo === id)
      let txtmsg = ''
      if (mensaje.length > 0) {
        txtmsg = '<b>Mensaje original: Enviado por: ' + mensaje[0].registrado + '<br>Fecha: ' + mensaje[0].fecha + '</b><br>' + mensaje[0].mensaje + '<br>'

        const msgChildren = this.dataMsgHijos.filter(item => item.padre === id)
        if (msgChildren.length > 0) {
          for (let index = 0; index < msgChildren.length; index++) {
            const element = msgChildren[index]
            const userenvio = '<b>Enviado por:</b> ' + (element.userid !== 0 ? element.nombreusaurio : element.registrado) + '<br>'
            txtmsg += '<br><b>' + 'msg: Rta: ' + userenvio + 'Fecha: ' + element.fecha + '</b><br>' + element.mensaje + '<br>'
          }
        }
      } else {
        mensaje = this.dataMsgHijos.filter(item => item.codigo === id)
        if (mensaje.length > 0) {
          const userenvio = 'Enviado por: ' + (mensaje[0].userid !== 0 ? mensaje[0].nombreusaurio : mensaje[0].registrado) + '<br>'
          txtmsg = '<b>msg: ' + userenvio + 'Fecha: ' + mensaje[0].fecha + '</b><br>' + mensaje[0].mensaje + '<br>'
        }
      }
      this.txtmensaje = txtmsg
      // document.getElementById('txtmsg').innerHTML = this.txtmensaje

      const { data } = await anunciosProcAdquisicion.actualizaMsgleidos({
        id: id
      })
      if (data.respuesta) {
        this.getMensajes()
      }
    },
    mostrarmensaje (item) {
      if (item.child) {
        if (item.padre !== 0) {
          this.idpadremsg = item.padre
        } else {
          this.idpadremsg = item.id
        }
        this.buscarMensaje(item.id)
      }
    },
    abrirDialogoNuevoMsg (esnuevomsg) {
      this.nuevomsg = esnuevomsg
      if (esnuevomsg === 1) {
        this.tituloanexarmsg = 'Nuevo Mensaje'
      } else {
        this.tituloanexarmsg = 'Agregar Respuesta'
      }
      this.txtnuevomsg = ''
      this.dialog_nuevomsg = true
    },
    async guardarMensaje () {
      const idpadre = this.nuevomsg === 1 ? 0 : this.idpadremsg
      const { data } = await anunciosProcAdquisicion.agregarMensaje({
        codigoconvreg: this.itemactual.Idpcr,
        mensaje: this.txtnuevomsg,
        padre: idpadre,
        estado: 'E',
        userid: this.$session.get('usuarioid')
      })
      if (data.respuesta) {
        this.$toasted.success('Se agregó la Respuesta con éxito', { icon: 'done', duration: 5000 })
        this.getMensajes()
        this.getMensajesHijos()
        this.dialog_nuevomsg = false
      } else {
        this.$toasted.error('Se presentó error al intentar de agregar el Mensaje', { icon: 'error', duration: 5000 })
      }
    }
  },
  watch: {
    itemactual: {
      handler: function (val) {
        if (val) {
          this.getMensajes()
          this.getMensajesHijos()
          this.txtmensaje = ''
          // this.$refs.txtmsg.innerHTML = ''
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
    this.getMensajes()
    this.getMensajesHijos()
  }
}
</script>
